<template>
  <div class="my-investments-page">
    <header class="page-header">
      <h1>üí∞ ÎÇ¥ Ìà¨Ïûê ÌòÑÌô©</h1>
      <p class="description">
        ÏÜîÌä∏Î©îÏù¥Ìä∏ÏôÄ Ìï®ÍªòÌïòÎäî ÎãπÏã†Ïùò ÏÜåÍ∏à Ìà¨Ïûê Ïó¨Ï†ïÏùÑ ÌïúÎààÏóê ÌôïÏù∏ÌïòÏÑ∏Ïöî.
      </p>
    </header>

    <main class="content-wrapper">
      <div v-if="loading" class="loading-state">
        <div class="spinner"></div>
        <p>Ìà¨Ïûê Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...</p>
      </div>

      <div v-else-if="error" class="error-state">
        <p class="error-message">
          üòî Ìà¨Ïûê Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§: {{ error }}
        </p>
        <button @click="fetchInvestments" class="retry-button">
          Îã§Ïãú ÏãúÎèÑ
        </button>
      </div>

      <div v-else-if="investments.length === 0" class="no-investments-state">
        <p>
          ÏïÑÏßÅ Ï∞∏Ïó¨Ìïú Ìà¨Ïûê ÏÉÅÌíàÏù¥ ÏóÜÏäµÎãàÎã§. ÏßÄÍ∏à Î∞îÎ°ú ÏÉàÎ°úÏö¥ Í∏∞ÌöåÎ•º Ï∞æÏïÑÎ≥¥ÏÑ∏Ïöî!
        </p>
        <router-link to="/shop" class="explore-button"
          >ÏÉàÎ°úÏö¥ Ìà¨Ïûê ÏÉÅÌíà ÌÉêÏÉâÌïòÍ∏∞</router-link
        >
      </div>

      <div v-else class="investments-display">
        <section class="investment-summary card glassmorphism">
          <h2>üìä ÎÇòÏùò Ï¥ù Ìà¨Ïûê ÏöîÏïΩ</h2>
          <div class="summary-grid">
            <div class="summary-item">
              <span class="label">Ï¥ù Ìà¨Ïûê Í∏àÏï°</span>
              <span class="value">{{ totalInvested.toLocaleString() }} Ïõê</span>
            </div>
            <div class="summary-item">
              <span class="label">ÌòÑÏû¨ Í∞ÄÏπò</span>
              <span class="value"
                >{{ totalCurrentValue.toLocaleString() }} Ïõê</span
              >
            </div>
            <div class="summary-item">
              <span class="label">Ï¥ù ÏàòÏùµÎ•†</span>
              <span :class="['value', overallReturnRateClass]"
                >{{ overallReturnRate.toFixed(2) }} %</span
              >
            </div>
            <div class="summary-item">
              <span class="label">Ï¥ù ÏòàÏÉÅ ÏàòÏùµ</span>
              <span class="value"
                >{{ totalExpectedProfit.toLocaleString() }} Ïõê</span
              >
            </div>
          </div>
        </section>

        <section class="investment-list">
          <h2>‚ú® ÎÇòÏùò Ìà¨Ïûê ÏÉÅÌíà</h2>
          <div class="investment-cards">
            <div
              v-for="investment in investments"
              :key="investment.id"
              class="investment-card card glassmorphism"
            >
              <div class="card-header">
                <h3 class="product-name">{{ investment.productName }}</h3>
                <span :class="['status-badge', investment.status]">{{
                  getStatusText(investment.status)
                }}</span>
              </div>
              <div class="card-body">
                <div class="info-row">
                  <span class="info-label">Ìà¨Ïûê Í∏àÏï°</span>
                  <span class="info-value"
                    >{{ investment.investedAmount.toLocaleString() }} Ïõê</span
                  >
                </div>
                <div class="info-row">
                  <span class="info-label">ÌòÑÏû¨ Í∞ÄÏπò</span>
                  <span class="info-value"
                    >{{ investment.currentValue.toLocaleString() }} Ïõê</span
                  >
                </div>
                <div class="info-row">
                  <span class="info-label">ÏàòÏùµÎ•†</span>
                  <span
                    :class="[
                      'info-value',
                      getReturnRateClass(investment.returnRate),
                    ]"
                  >
                    {{ investment.returnRate.toFixed(2) }} %
                  </span>
                </div>
                <div class="info-row">
                  <span class="info-label">Ìà¨ÏûêÏùº</span>
                  <span class="info-value">{{
                    formatDate(investment.investmentDate)
                  }}</span>
                </div>
                <div class="info-row" v-if="investment.expectedReturnDate">
                  <span class="info-label">ÏòàÏÉÅ ÌöåÏàòÏùº</span>
                  <span class="info-value">{{
                    formatDate(investment.expectedReturnDate)
                  }}</span>
                </div>

                <div
                  class="progress-section"
                  v-if="investment.progress !== undefined"
                >
                  <span class="info-label"
                    >ÏßÑÌñâÎèÑ ({{ investment.progress }}%)</span
                  >
                  <div class="progress-bar-container">
                    <div
                      class="progress-bar"
                      :style="{ width: investment.progress + '%' }"
                    ></div>
                  </div>
                </div>

                <div
                  class="payout-info"
                  v-if="
                    investment.payoutHistory &&
                    investment.payoutHistory.length > 0
                  "
                >
                  <span class="info-label"
                    >ÏµúÍ∑º Î∞∞Îãπ:
                    {{
                      investment.payoutHistory[
                        investment.payoutHistory.length - 1
                      ].amount.toLocaleString()
                    }}Ïõê</span
                  >
                  <span class="info-value"
                    >({{
                      formatDate(
                        investment.payoutHistory[
                          investment.payoutHistory.length - 1
                        ].date
                      )
                    }})</span
                  >
                </div>
              </div>
              <div class="card-footer">
                <router-link
                  :to="`/investment-detail/${investment.id}`"
                  class="detail-button"
                >
                  ÏÉÅÏÑ∏ Î≥¥Í∏∞ <i class="fas fa-chevron-right"></i>
                </router-link>
              </div>
            </div>
          </div>
        </section>
      </div>
    </main>
  </div>
</template>

<script>
import { ref, onMounted, computed } from "vue";
import { useRouter } from "vue-router"; // useRouterÍ∞Ä ÌïÑÏöîÌïòÎã§Î©¥
import { auth, db } from "@/firebaseConfig";
import { onAuthStateChanged } from "firebase/auth";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  name: "MyInvestmentsPage",
  setup() {
    const router = useRouter(); // ÎùºÏö∞ÌÑ∞ ÌïÑÏöîÏãú
    const investments = ref([]);
    const loading = ref(true);
    const error = ref(null);

    // Í∞ÄÏÉÅ Îç∞Ïù¥ÌÑ∞ (FirestoreÏóêÏÑú Î∂àÎü¨Ïò¨ Îç∞Ïù¥ÌÑ∞ Íµ¨Ï°∞Î•º ÎØ∏Î¶¨ Ï†ïÏùò)
    // Ïã§Ï†ú Firestore Îç∞Ïù¥ÌÑ∞ÏôÄ ÏùºÏπòÏãúÏºúÏïº Ìï©ÎãàÎã§.
    const getDummyInvestments = (userId) => {
      // Ïã§Ï†ú Ïï±ÏóêÏÑúÎäî userIdÏóê Îî∞Îùº FirestoreÏóêÏÑú Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏ÏòµÎãàÎã§.
      // Ïó¨Í∏∞ÏÑúÎäî ÏòàÏãúÎ•º ÏúÑÌï¥ ÎçîÎØ∏ Îç∞Ïù¥ÌÑ∞Î•º Î∞òÌôòÌï©ÎãàÎã§.
      return [
        {
          id: "inv001",
          userId: userId,
          productName: "ÏÜîÌä∏Ìåú 1Ìò∏ ÏÉùÏÇ∞ Ìà¨Ïûê",
          investedAmount: 1000000, // Ïõê
          currentValue: 1050000, // Ïõê (5% ÏàòÏùµ)
          returnRate: 5.0, // %
          status: "active", // active, completed, pending, cancelled
          investmentDate: new Date("2024-01-15"),
          expectedReturnDate: new Date("2025-01-15"),
          progress: 75, // Î∞±Î∂ÑÏú®
          payoutHistory: [
            { amount: 10000, date: new Date("2024-04-15") },
            { amount: 10000, date: new Date("2024-07-15") },
          ],
        },
        {
          id: "inv002",
          userId: userId,
          productName: "ÏÜîÌä∏Ïõ®Ïñ¥ÌïòÏö∞Ïä§ ÌôïÏû• ÌéÄÎìú",
          investedAmount: 2500000,
          currentValue: 2450000, // Ïõê (-2% ÏÜêÏã§)
          returnRate: -2.0,
          status: "active",
          investmentDate: new Date("2024-03-01"),
          expectedReturnDate: new Date("2026-03-01"),
          progress: 50,
          payoutHistory: [],
        },
        {
          id: "inv003",
          userId: userId,
          productName: "ÏóºÏ†Ñ ÌòÑÎåÄÌôî ÌîÑÎ°úÏ†ùÌä∏",
          investedAmount: 500000,
          currentValue: 580000, // Ïõê (16% ÏàòÏùµ)
          returnRate: 16.0,
          status: "completed",
          investmentDate: new Date("2023-05-10"),
          expectedReturnDate: new Date("2024-05-10"),
          payoutHistory: [
            { amount: 20000, date: new Date("2023-08-10") },
            { amount: 20000, date: new Date("2023-11-10") },
            { amount: 20000, date: new Date("2024-02-10") },
            { amount: 520000, date: new Date("2024-05-10") }, // ÏõêÍ∏à ÌöåÏàò Ìè¨Ìï®
          ],
        },
        {
          id: "inv004",
          userId: userId,
          productName: "ÏπúÌôòÍ≤Ω ÏÜåÍ∏à ÏÉùÏÇ∞ ÏÑ§ÎπÑ",
          investedAmount: 1500000,
          currentValue: 1500000,
          returnRate: 0.0,
          status: "pending", // ÏïÑÏßÅ ÌôúÏÑ±Ìôî ÎåÄÍ∏∞ Ï§ë
          investmentDate: new Date("2025-01-01"),
          expectedReturnDate: null,
          progress: 0,
          payoutHistory: [],
        },
      ];
    };

    const fetchInvestments = async () => {
      loading.value = true;
      error.value = null;
      try {
        const user = await new Promise((resolve) => {
          const unsubscribe = onAuthStateChanged(auth, (u) => {
            unsubscribe();
            resolve(u);
          });
        });

        if (!user) {
          router.push("/login"); // Î°úÍ∑∏Ïù∏ÎêòÏßÄ ÏïäÏïòÎã§Î©¥ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
          return;
        }

        // Ïã§Ï†ú FirestoreÏóêÏÑú Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞ (ÎçîÎØ∏ Îç∞Ïù¥ÌÑ∞ ÎåÄÏã† ÏÇ¨Ïö©)
        const investmentsCollectionRef = collection(db, "investments");
        const q = query(
          investmentsCollectionRef,
          where("investorUID", "==", user.uid) // 'userId' ÎåÄÏã† 'investorUID'Î°ú Î≥ÄÍ≤Ω
        );
        const querySnapshot = await getDocs(q);

        const fetchedData = [];
        querySnapshot.forEach((doc) => {
          fetchedData.push({ id: doc.id, ...doc.data() });
        });
        investments.value = fetchedData;

        // ÎßåÏïΩ FirestoreÏóê Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÍ±∞ÎÇò ÌÖåÏä§Ìä∏Î•º ÏúÑÌï¥ ÎçîÎØ∏ Îç∞Ïù¥ÌÑ∞Î•º ÏÇ¨Ïö©ÌïòÎ†§Î©¥ ÏïÑÎûò Ï§Ñ ÌôúÏÑ±Ìôî
        if (fetchedData.length === 0) {
          investments.value = getDummyInvestments(user.uid);
        }
      } catch (err) {
        console.error("Ìà¨Ïûê Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§:", err);
        error.value = err.message || "Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
      } finally {
        loading.value = false;
      }
    };

    // Ï¥ù Ìà¨Ïûê Í∏àÏï° Í≥ÑÏÇ∞
    const totalInvested = computed(() => {
      return investments.value.reduce(
        (sum, inv) => sum + inv.investedAmount,
        0
      );
    });

    // Ï¥ù ÌòÑÏû¨ Í∞ÄÏπò Í≥ÑÏÇ∞
    const totalCurrentValue = computed(() => {
      return investments.value.reduce((sum, inv) => sum + inv.currentValue, 0);
    });

    // Ï¥ù ÏàòÏùµÎ•† Í≥ÑÏÇ∞
    const overallReturnRate = computed(() => {
      if (totalInvested.value === 0) return 0;
      return (
        ((totalCurrentValue.value - totalInvested.value) /
          totalInvested.value) *
        100
      );
    });

    // Ï¥ù ÏòàÏÉÅ ÏàòÏùµ (ÌòÑÏû¨ Í∞ÄÏπò - Ìà¨Ïûê Í∏àÏï°)
    const totalExpectedProfit = computed(() => {
      return totalCurrentValue.value - totalInvested.value;
    });

    // Ï¥ù ÏàòÏùµÎ•†Ïóê Îî∞Î•∏ ÌÅ¥ÎûòÏä§ (ÏÉâÏÉÅ Î≥ÄÍ≤Ω)
    const overallReturnRateClass = computed(() => {
      if (overallReturnRate.value > 0) return "positive";
      if (overallReturnRate.value < 0) return "negative";
      return "";
    });

    const getStatusText = (status) => {
      switch (status) {
        case "active":
          return "ÏßÑÌñâ Ï§ë";
        case "completed":
          return "ÏôÑÎ£åÎê®";
        case "pending":
          return "ÏäπÏù∏ ÎåÄÍ∏∞ Ï§ë";
        case "cancelled":
          return "Ï∑®ÏÜåÎê®";
        default:
          return "Ïïå Ïàò ÏóÜÏùå";
      }
    };

    const getReturnRateClass = (rate) => {
      if (rate > 0) return "positive";
      if (rate < 0) return "negative";
      return "";
    };

    const formatDate = (dateInput) => {
      if (!dateInput) return "";
      // Firebase Timestamp Í∞ùÏ≤¥ Ï≤òÎ¶¨
      if (dateInput.toDate) {
        return dateInput.toDate().toLocaleDateString("ko-KR");
      }
      // JavaScript Date Í∞ùÏ≤¥ Ï≤òÎ¶¨
      if (dateInput instanceof Date) {
        return dateInput.toLocaleDateString("ko-KR");
      }
      return String(dateInput); // Í∑∏ Ïô∏Ïùò Í≤ΩÏö∞ Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò
    };

    onMounted(() => {
      fetchInvestments();
    });

    return {
      investments,
      loading,
      error,
      totalInvested,
      totalCurrentValue,
      overallReturnRate,
      overallReturnRateClass,
      totalExpectedProfit,
      getStatusText,
      getReturnRateClass,
      formatDate,
      fetchInvestments, // retry Î≤ÑÌäºÏùÑ ÏúÑÌï¥ ÎÖ∏Ï∂ú
    };
  },
};
</script>

